import logo from "../../assets/logo dark bg.svg";
// import banner from "../../assets/mobile-banner.jpg";
import ties from "../../assets/tie.jpg";
import belts from "../../assets/belt.jpg";
import shoes from "../../assets/brown-shoe.jpg";
import { FiBookmark } from "react-icons/fi";
import { FiShoppingCart } from "react-icons/fi";
import { BsChevronRight } from "react-icons/bs";
import "./Home.scss";

function Home() {
  return (
    <div className="home">
      <div className="banner-wrapper">
        {/* <img className="banner" src={banner} alt="" /> */}
        <nav className="navbar">
          <img className="logo" src={logo} alt="logo" />
          <div className="nav-items">
            <FiBookmark className="wishlist-icon"></FiBookmark>
            <FiShoppingCart className="cart-icon"></FiShoppingCart>
          </div>
        </nav>
        <div className="hero-container">
          <h1 className="hero-heading">
            Wear what we are proud of, Best linen collection in the world
          </h1>
          <p className="hero-sub-heading">
            Made of the Finest Natural Cotton with a Cutting Edge, Contemporary
            Design
          </p>

          <button className="cta">
            <p className="btn-text">Explore Collection</p>
            <BsChevronRight className="right-arrow"></BsChevronRight>
          </button>
        </div>
      </div>

      {/* Categories */}
      <div className="categories-container">
        <p className="categories-heading">Categories</p>

        <div className="category-slider">
          <div className="category-box">
            <img src={ties} alt="" />
            <p className="category-box-label">Ties</p>
          </div>
          <div className="category-box">
            <img src={belts} alt="" />
            <p className="category-box-label">Belts</p>
          </div>
          <div className="category-box category-box-last">
            <img src={shoes} alt="" />
            <p className="category-box-label">Shoes</p>
          </div>
        </div>
      </div>

      {/* Featured */}

      <div className="featured-container">
        <p className="featured-heading">Featured</p>
      </div>
    </div>
  );
}

export default Home;
